.rules {
    margin: 60px auto;
    width: 80%;
    max-width: 850px;
    height: 80vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .instructions-box {
        background-color: #eee;
        border: 2px solid #333;
        border-radius: 5px;

        width: 70%;
        height: 70%;

        padding: 20px;
        margin: 20px;
        box-sizing: border-box;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        text-align: center;
        font-size: 1.2em;

        img {
            width: 100%;
            max-width: 725px;
        }

        .instruction-page-buttons {
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
        }
    }
}