.rules{margin:60px auto;width:80%;max-width:850px;height:80vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.rules .instructions-box{background-color:#eee;border:2px solid #333;border-radius:5px;width:70%;height:70%;padding:20px;margin:20px;box-sizing:border-box;display:flex;flex-direction:column;align-items:center;justify-content:space-around;text-align:center;font-size:1.2em}.rules .instructions-box img{width:100%;max-width:725px}.rules .instructions-box .instruction-page-buttons{width:100%;display:flex;flex-direction:row;justify-content:space-around}
.turn-tracker{position:absolute;margin:auto;width:500px;height:60px;display:flex;flex-direction:row;align-items:center;justify-content:center;font-size:2em}.turn-tracker.play,.turn-tracker.discard,.turn-tracker.think{border:1px solid #333;border-radius:5px;background-color:#ddda}.turn-tracker span{padding:10px}
.hand-stats{position:absolute;width:240px;height:50px;display:flex;flex-direction:row;align-items:center;justify-content:center;font-size:.9em;text-align:center;border:1px solid #333;border-radius:5px;background-color:#ddda}.hand-stats span{padding:10px}.hand-stats span.bold{font-weight:600}.hand-stats span.icon{position:absolute;bottom:-50px;left:50%;width:100px;transform:translateX(-60px);font-size:3em}
.help-button{position:absolute;bottom:5px;right:5px;padding:10px;font-family:monospace}.help-button:active{position:absolute;top:unset;bottom:4px}
.offboarding{margin:60px auto;width:80%;height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid #333;border-radius:5px;background-color:#ddda}
.App{width:100vw;height:100vh}button{padding:16px 32px;background-color:#ddda;border:1px solid #333;border-radius:5px;box-shadow:2px 2px 1px 1px #3339;font-size:1em;font-weight:600;cursor:pointer}button:hover:not(:disabled){background-color:#fffa}button:active{box-shadow:1px 1px 1px 1px #3339}button:disabled{background-color:#ccca;cursor:not-allowed}.Toastify .toaster{position:absolute;top:40%}.Toastify .toaster div.info{background-color:#ffc759;border:1px solid #fff;border-radius:5px;color:#333;font-size:2em;font-weight:600;text-align:center}.highlight{animation:2s ease-in-out 1s infinite alternate glow}@keyframes glow{from{transform:translateY(0px);box-shadow:0 0 10px 5px #fff}to{transform:translateY(5px);box-shadow:0 0 5px 8px #fff}}
body {
  margin: 0;
  box-sizing: border-box;

  font-family: 'Spartan', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #888;
  background-size: auto;
  background-repeat: no-repeat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

